
h1,
h2,
h3,
h4,
h5,
p,
a,
body {
  font-family: 'Roboto', sans-serif;
  margin: 0;
  font-weight: normal;
  color: #a4a4a4;
}
body {
  background: #1d1d1b;
}

h1.title {
  font-size: 55px;
  margin: 50px 0;
  text-align: center;
  font-weight: normal;
  color: #fff;
}
.worldCup .match-card {
  width: 100%;
  max-width: 600px;
  padding: 20px;
  background: #292929;
  margin: 0 auto 30px;
  display: block;
  border-radius: 10px;
  -webkit-box-shadow: 0px 15px 36px 4px rgba(0, 0, 0, 0.5);
          box-shadow: 0px 15px 36px 4px rgba(0, 0, 0, 0.5);
}
.worldCup .match-card h2 {
  font-size: 24px;
  font-weight: normal;
  margin: 0;
}
.worldCup .match-card .match-info h4 {
    font-size: 24px;
    font-weight: 900;
    color: #df0714;
}
.worldCup .match-card .match-info span {
  font-size: 10px;
}
.worldCup .match-card table {
  width: 100%;
  margin-top: 20px;
}
.worldCup .match-card table tr td {
  width: 40%;
  text-align: center;
  padding-bottom: 50px;
}
.worldCup .match-card table tr:last-child() td {
  padding-bottom: 0;
}
.worldCup .match-card table td:nth-child(2) {
  width: 20%;
}
.worldCup .match-card table td h4 {
  font-size: 26px;
}
.worldCup .match-card table td h5 {
  font-size: 20px;
}

